<template>
	<v-container fluid style="height: 100vh; padding: 0">
		<splitpanes vertical>
			<pane>
				<tree
					style="height: 100vh"
					ref="tree"
					:identifier="getId"
					:data="tree"
					node-text="name"
					type="cluster"
					layoutType="circular"
					v-model="highlightedNode"
					:duration="duration"
					:zoomable="zoomable"
					@clickedText="onClickNode"
					@clickedNode="onClickNode"
				>
					<template #behavior="{ on, actions }">
						<CollapseOnClick v-bind="{ on, actions }" />
					</template>
				</tree>
			</pane>
			<pane style="height: 100vh;overflow-y:scroll;">
				<v-card fluid>
					<v-card-text>
						<p>Focusing {{focusNodeName}}</p>
						<p v-for="(el, idx) in mentionedSentences" :key="idx">
							{{ el }}
						</p>
					</v-card-text>
				</v-card>
			</pane>
		</splitpanes>
	</v-container>
</template>

<script>
import axios from "axios";
import constants from "@/constants.js";
import { Splitpanes, Pane } from "splitpanes";
import { tree } from "vued3tree";
import "splitpanes/dist/splitpanes.css";
import CollapseOnClick from "@/components/CollapseOnClick";
export default {
	name: "HelloWorld",
	data() {
		return {
			duration: 0,
			currentData: null,
			highlightedNode: null,
			tree: null,
			zoomable: true,
			mentionedSentences: [],
			focusNode:null
		};
	},
	methods: {
		async do(action) {
			if (this.currentData) {
				this.isLoading = true;
				await this.$refs["tree"][action](this.currentData);
				this.isLoading = false;
			}
		},
		getHACTree() {
			axios({
				baseURL: constants.baseURL,
				url: "/api/v1/hac_tree",
				data: {},
				params: {},
			}).then(
				(success) => {
					console.log(success);
					this.tree = success.data;
				},
				(fail) => {
					console.log(fail);
				}
			);
		},
		getId(node) {
			return node.id;
		},
		dfsGetAllSentences(relevantSentences, rootNode) {
			if (rootNode && rootNode.aux.original_text) {
				relevantSentences.push(rootNode.aux.original_text);
			}
			for (const child of rootNode.children) {
				this.dfsGetAllSentences(relevantSentences, child);
			}
		},
		onClickNode(evt) {
			const relevantSentences = [];
			const clickedNode = evt.data;
			this.dfsGetAllSentences(relevantSentences, clickedNode);
			this.mentionedSentences = relevantSentences;
			this.focusNode = clickedNode;
		},
	},
	computed: {
		focusNodeName(){
			if(this.focusNode){
				return this.focusNode.name;
			}
			return "null";
		}
	},
	components: {
		Splitpanes,
		Pane,
		tree,
		CollapseOnClick,
	},
	watch: {},
	mounted() {
		this.getHACTree();
	},
};
</script>

<style>
.splitpanes {
	background-color: #f8f8f8;
}

.splitpanes__splitter {
	background-color: #ccc;
	position: relative;
}
.splitpanes__splitter:before {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	transition: opacity 0.4s;
	background-color: rgba(255, 0, 0, 0.3);
	opacity: 0;
	z-index: 1;
}

.splitpanes--vertical > .splitpanes__splitter {
	width: 10px;
}
.splitpanes--horizontal > .splitpanes__splitter {
	height: 10px;
}

.splitpanes__splitter:hover:before {
	opacity: 1;
}

.splitpanes--vertical > .splitpanes__splitter:before {
	left: -5px;
	right: -5px;
	height: 100%;
}
.splitpanes--horizontal > .splitpanes__splitter:before {
	top: -5px;
	bottom: -5px;
	width: 100%;
}
</style>
